<template>
  <div id="bottomPie1"></div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      myChart: null
    };
  },
  mounted() {
    this.loadChart();
  },

  methods: {
    loadChart(title,data) {
      this.myChart = echarts.init(document.getElementById("bottomPie1"));
      this.myChart.setOption(this.initOption(title,data));
    },
    init(data){
      var title = [];
      var dataArr = [];
      for(var i in data){
        title.push(data[i].name);
        dataArr.push({ value: Number((data[i].power.epercent*100).toFixed(2)), name: data[i].name })
      }
      this.loadChart(title,dataArr);
    },

    initOption(title,data) {
      return {
        title: {
          text: "电抗能力对比",
          subtext: "",
          textStyle: {
            color: "#00FFFF"
          },
          left: "center"
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          bottom: 10,
          left: "center",
          textStyle: {
            color: "#00FFFF"
          },
          data: title
        },
        color: ["#ffc000", "#a5a5a5", "#ed7d31", "#5b9bd5"],
        series: [
          {
            name: "访问来源",
            type: "pie",
            radius: "55%",
            center: ["50%", "50%"],
            data: data,
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };
    }
  }
};
</script>
<style lang="less">
#bottomPie1 {
  width: 100%;
  height: 100%;
}
</style>
