<template>
  <div :id="id" class="orderArea"></div>
</template>

<script>
import echarts from "echarts";
import echartsTheme from "@/components/echarts/theme/westeros.json";

export default {
  name: "radarChart12",
  data() {
    return {
      id: "radarChart12",
      myChart: null
    };
  },
  mounted() {
  },
  beforeDestroy() {
    if (!this.myChart) {
      return;
    }
    this.myChart.dispose();
    this.myChart = null;
  },
  methods: {
    loadChart(title, dataArr, list) {
      this.$nextTick(() => {
        this.myChart = echarts.init(document.getElementById(this.id));
        this.myChart.setOption(this.initOption(title, dataArr, list));
      });
    },
    init(data) {
      var title = [];
      var dataArr = [];
      var list = [];
      var color = [
        "#87DE75",
        "#5FB4FA",
        "#3b5999",
        "#a9d86e",
        "#FF6C60",
        "#18a689"
      ];
      for (var i in data) {
        title.push(data[i].name);
        dataArr.push({ text: data[i].name, max: 100, color: color[i] });
        list.push(Number((data[i].power.hitpercent*100).toFixed(2)));
        
      }
      this.loadChart(title, dataArr, list);
    },
    initOption(title, dataArr, list) {
      let option = {
        title: {
          text: "攻击能力对比（单位%）",
          textStyle: {
            color: "#00FFFF"
          },
          subtext: "",
          left: "60px",
          top: "0px"
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: title
        },
        radar: [
          {
            name: {
              fontSize: 11 // 顶点字体大小 不能设置 radio 否则不能生效
            },
            splitLine: {
              lineStyle: {
                color: "#00aaff" // 每一圈的边界颜色
              }
            },
            axisLabel: {
              inside: true
            },
            axisLine: {
              lineStyle: {
                color: "#00aaff" // 半径线颜色
              }
            },
            splitArea: {
              areaStyle: {
                // color: ['#00aaff', '#0099ff', '#00aaff', '#0099ff', '#00aaff'] // 每一圈的颜色
              }
            },
            indicator: dataArr
          }
        ],
        series: [
          {
            type: "radar",
            data: [
              {
                value: list,
                areaStyle: {
                  normal: {
                    opacity: 0.8, // 图表透明度
                    color: "#87DE75" // 图表颜色
                  }
                },
                lineStyle: {
                  color: "#6397ff" // 图表边框颜色
                },
                label: {
                  normal: {
                    show: true,
                    color: "#6397ff", // 顶点数字颜色
                    fontSize: 16, // 顶点数字大小
                    formatter: function(params) {
                      return params.value;
                    }
                  }
                }
              }
            ]
          }
        ]
      };
      return option;
    }
  },
  watch: {}
};
</script>

