<template>
  <section class="data_section" ref="data_section">
    <h3>能力对比</h3>
    <div class="headerData">
      <el-date-picker
        v-model="value1"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
      ></el-date-picker>
      <el-cascader v-model="nameArr"  :options="options" :props="props" clearable></el-cascader>
      <!-- <el-select v-model="nameArr" multiple placeholder="请选择">
        <el-option v-show="item.mode !='1'" v-for="item in options" :key="item._id" :label="item.name" :value="item._id"></el-option>
      </el-select> -->
      <el-button type="primary" class="ksdb" @click="search">开始比对</el-button>
    </div>
    <el-row :gutter="10" class="row_list order_list">
      <el-col :span="24" class="bottomPie">
        <el-col :span="12">
          <bottomPie1 ref="bottomPie1"></bottomPie1>
        </el-col>
        <el-col :span="12">
          <bottomPie2 ref="bottomPie2"></bottomPie2>
        </el-col>
      </el-col>
    </el-row>
    <el-row :gutter="10" class="row_list order_list">
      <el-col :span="8">
        <pieData ref="pieData"></pieData>
      </el-col>
      <el-col :span="8">
        <pieData12 ref="pieData12"></pieData12>
      </el-col>
      <el-col :span="8">
        <pieData15 ref="pieData15"></pieData15>
      </el-col>
    </el-row>
  </section>
</template>

<script>
import { vs } from "@/api/vs";
import pieData from "./components/pieData";
import pieData12 from "./components/pieData12";
import pieData15 from "./components/pieData15";
import bottomPie1 from "./components/bottomPie1";
import bottomPie2 from "./components/bottomPie2";
export default {
  components: {
    pieData,
    pieData12,
    pieData15,
    bottomPie1,
    bottomPie2
  },
  data() {
    return {
      value1: "",
      nameArr: "",
      props: {
        label: "name",
        multiple: true,
        value: "name"
      },
      name: "",
      data: {
        uid: "",
        name: "",
        site: "2",
        start: "",
        end: ""
      },
      options: []
    };
  },
  created() {},
  mounted() {
    this.initUser();
  },
  methods: {
    initUser(){
      var user = this.sessionData("get", "userListAll");
      var arr = new Array();
      for(var i in user){ 
        if(user[i].mode != "1"){
          arr.push(user[i]);
        }
      }
      this.options = arr;
    },

    search(s) {
      var st = this.value1 ? this.value1[0].getTime() : "";
      var et = this.value1 ? this.value1[1].getTime() : "";
      this.data.name = this.nameArr.constructor == String? this.nameArr: this.nameArr.join(",");
      this.init(this.data.name, s || this.data.site, st, et);
    },
    selectGet(vId) {
      let obj = {};
      obj = this.options.find(item => {
        return item._id === vId;
      });
      this.data.name = obj.name;
    },
    init(name, site, start, end) {
      vs({
        name: name,
        site: site,
        start: start,
        end: end
      }).then(res => {
        if (res.code == 0) {
          this.$message({
            message: "数据查询成功！",
            type: "success"
          });
          var list = this.$unfly(res.data);

          this.$refs.bottomPie1.init(list);
          this.$refs.bottomPie2.init(list);
          this.$refs.pieData15.init(list);
          this.$refs.pieData12.init(list);
          this.$refs.pieData.init(list);

          console.log("查看能力对比数据", list);
        }
      });
    }
  }
};
</script>

